<template>
	<div>

		<el-breadcrumb separator="/">
			<el-breadcrumb-item>系统管理</el-breadcrumb-item>
			<el-breadcrumb-item>评分</el-breadcrumb-item>
		</el-breadcrumb>
		<el-card>
			<div style="margin-bottom: 12px">
				<el-button type="primary" @click="handleValidate">批量发布</el-button>
				<el-button type="danger" @click="toggleSelection()">取消选择</el-button>
			</div>
			<el-divider></el-divider>

			<el-table-editabled v-model="tableData" :columns="['auditLevel', 'evaluate']" :validators="validators" ref="elTableEditabled">
				<el-table :data="tableData" style="width: 100%" @selection-change="handleSelectionChange" ref="multipleTable" max-height="1000px">
					<el-table-column type="selection"></el-table-column>
					<el-table-column fixed="" prop="recordId" label="ID">
					</el-table-column>
					<el-table-column prop="name" label="姓名">
					</el-table-column>
					<el-table-column prop="studentId" label="学号">
					</el-table-column>
					<el-table-column prop="auditLevel" label="等级">
						<template slot-scope="{ row }">
							<el-table-editabled-cell :row="row" prop="auditLevel">
								<template slot-scope="{ rowStates, validateOwn }">
									<span v-show="!rowStates.editing">{{row.auditLevel}}</span>
									<el-select v-model="row.auditLevel" placeholder="请选择" @change="validateOwn" v-show="rowStates.editing">
										<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
										</el-option>
									</el-select>
								</template>
							</el-table-editabled-cell>
						</template>
					</el-table-column>
					<el-table-column prop="evaluate" label="评价">
						<template slot-scope="{ row }">
							<el-table-editabled-cell :row="row" prop="evaluate">
								<template slot-scope="{ rowStates, validateOwn }">
									<span v-show="!rowStates.editing">{{row.evaluate}}</span>
									<el-input v-show="rowStates.editing" v-model="row.evaluate" clearable @blur="validateOwn">
									</el-input>
								</template>
							</el-table-editabled-cell>
						</template>
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="120">
						<template slot-scope="{ row }">
							<el-table-editabled-cell :row="row">
								<template slot-scope="{ rowStates }">
									<el-button v-show="!rowStates.editing" @click="handleEdit(row)" type="text" size="small">
										编辑
									</el-button>
									<div v-show="rowStates.editing">
										<el-button @click="handleCanelRow(row)" type="text" size="small">
											取消
										</el-button>
										<el-button @click="handleSave(row)" type="text" size="small">
											保存
										</el-button>
									</div>
								</template>
							</el-table-editabled-cell>
						</template>
					</el-table-column>
				</el-table>
			</el-table-editabled>
		</el-card>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				multipleSelection: [],
				multiarr: [],
				options: [{
					value: '优秀',
					label: '优秀'
				}, {
					value: '良好',
					label: '良好'
				}, {
					value: '一般',
					label: '一般'
				}, {
					value: '合格',
					label: '合格'
				}, {
					value: '不合格',
					label: '不合格'
				}],
				tableData: [{
					auditLevel: '不及格',
					evaluate: '',
					studentId: 120181080404,
					name: '小明',
					recordId: '001'
				}, {
					auditLevel: '优秀',
					evaluate: '王小虎',
					studentId: 120181080404,
					name: '小明',
					recordId: '002'
				}, {
					auditLevel: '一般',
					evaluate: '王小虎',
					studentId: 120181080404,
					name: '小明',
					recordId: '003'
				}],
				validators: {
					auditLevel({
						auditLevel
					}, next) {
						if (!auditLevel) {
							next('请选择等级')
						} else {
							next('')
						}
					}

				}
			}
		},
		methods: {
			toggleSelection() {
				this.$refs.multipleTable.clearSelection();
			},
			handleEdit(row) {
				this.$refs.elTableEditabled.editRows([row])
			},
			handleCanelRow(row) {
				this.$refs.elTableEditabled.cancelRows([row])
			},
			handleSave(row) {
				// 验证一些正在编辑的行
				this.$refs.elTableEditabled.validateRows([row], valid => {
					// valid 为布尔值 代表表格正在编辑的所有字段是否验证通过
					if (valid) {
						this.$refs.elTableEditabled.saveRows([row]);
					}
				})
			},
			handleValidate() {

				this.multiarr = [];
				// 验证表格中所有勾选的字段
				for (var i = 0; i < this.multipleSelection.length; i++) {
					if (!this.multipleSelection[i].auditLevel || (this.multipleSelection[i].auditLevel == '不及格' && !this.multipleSelection[
							i].evaluate)) {
						return this.$message({
							message: '勾选的字段中存在无等级或不及格未填写原因项',
							type: 'warning'
						});
					} else {
						for (var i = 0; i < this.multipleSelection.length; i++) {
							var k = {
								recordId: '',
								evaluate: '',
								auditLevel: ''
							}
							k.recordId = this.multipleSelection[i].recordId;
							k.auditLevel = this.multipleSelection[i].auditLevel;
							k.evaluate = this.multipleSelection[i].evaluate;
							this.multiarr.push(k);
						}
						return this.$message({
							message: '更新成功',
							type: 'success'
						});
					}
				}
			},
			handleSelectionChange(val) {
				this.multipleSelection = val;
			},

		}
	}
</script>
<style scoped="scoped">
	.el-breadcrumb {
		margin-bottom: 15px;
	}
</style>
